import { Controller, useForm } from "react-hook-form";
import { useState } from "react";
import { Text, View, TextInput, StyleSheet, Image, Button, ScrollView, KeyboardAvoidingView, Platform } from "react-native";
import { useDispatch } from "react-redux";
import { TextInputMask } from 'react-native-masked-text'
import { useNavigation } from "@react-navigation/native"
import { NativeStackNavigationProp } from "@react-navigation/native-stack"

type RootStackParams = {
  Assinatura:any,
}
const Message = (message:String) => {
  return (
    <View>
      <Text style={{color:'red'}}>{message}</Text>
    </View>
  )
}


function SignatureStatement () {
  
  const dispatch = useDispatch()
  const navigation = useNavigation<NativeStackNavigationProp<RootStackParams>>()

  const {handleSubmit, control, formState:{errors}} = useForm()

  const onSubmit = (data:any) => {

    dispatch({
      type:'SET_STATEMENT',
      payload: {
        name: data.name,
        cpf: data.cpf,
        id:data.rg,
        exam:data.exam,
        company:data.nameCompany,
        companyCpf:data.cpfCompany
      }
    })
    navigation.navigate('Assinatura')
  }
  
  const selectMessage = (data:string) =>{
    return Message(data)
  }

  const setData = () => {

    navigation.navigate('Assinatura')
   }
  return (
    <KeyboardAvoidingView behavior={Platform.select({ios:'padding'})} 
    keyboardVerticalOffset={Platform.select({ios:100})}>
    <ScrollView style={{backgroundColor:"white", height:'100%'}}>
    <View style={{backgroundColor:"white",alignItems: "center"}}>
      <Image
      resizeMode={"contain"}
      style={{ width: "90%", height:100}}
      source={require('./assets/support.png')} />
      <Text style={styles.Title}>Termo de Consentimento informado para Exames Endoscópicos e Anestesista</Text>
    </View>
    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>Por tanto, eu:</Text>
      <Controller 
        control={control}
        name='name'
        render={({field: {onChange, value}})=>(
          <TextInput 
          placeholder="Digite aqui seu nome"
          style={styles.input} 
          value={value} 
          onChangeText={value=>onChange(value)}
          />
        )}
        rules={{
          required:{
            value:true,
            message:'Digite seu nome'
          }
        }}
      />
        {
          errors.name?.message ? selectMessage(errors.name?.message):null
        }
    </View>
    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>Cpf:</Text>
      <Controller 
        control={control}
        name="cpf"
        render={({field: {onChange, value}})=>(
          <TextInputMask 
          type={'cpf'} 
          placeholder="Digite aqui seu CPF"
          style={styles.input} 
          value={value} 
          onChangeText={value => onChange(value)}
          />
        )} 
        rules={{
          required:{
            value:true,
            message:'Digite seu cpf'
          },
          pattern:{
            value:/^([0-9]{3}\.?[0-9]{3}\.?[0-9]{3}\-?[0-9]{2}|[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2})$/,
            message:'Digite um cpf valido'
          }
        }}
      />
      {errors.cpf?.message ? selectMessage(errors.cpf?.message):null}
    </View>

    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>Identidade:</Text>
      <Controller 
        control={control}
        name='rg'
        render={({field: {onChange, value}})=>(
          <TextInputMask
            placeholder="Digite aqui sua Identidade" 
            type={'only-numbers'} 
            style={styles.input} 
            value={value} 
            onChangeText={value=>onChange(value)}/>
        )}
        rules={{
          required:{
            value:true,
            message:'Digite seu RG'
          }
        }}
      />
      {
        errors.rg?.message? selectMessage(errors.rg?.message) :null
      }
    </View>

    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>Estou ciente que serei submetido ao exame de:</Text>
      <Controller 
        control={control}
        name='exam'
        render={({field: {onChange, value}})=>(
          <TextInput 
            placeholder="Digite aqui o exame"
            style={styles.input} 
            value={value} 
            onChangeText={value=>onChange(value)}
          />
        )}
        rules={{
          required: {
            value:true,
            message:'Digite o exame que vai realizar'
          }
        }}
      />
      {
        errors.exam?.message ? selectMessage(errors.exam?.message) :null
      }
    </View>

    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>E que meu acompanhante</Text>
      <Controller 
        control={control}
        name='nameCompany'
        render={({field: {onChange, value}})=>(
          <TextInput 
          placeholder="Digite aqui o nome do seu company"
          style={styles.input} 
          value={value} 
          onChangeText={value=>onChange(value)}
          />
        )}
        rules={{
          required:{
            value:true,
            message:'Digite o nome do seu acompanhante'
          }
        }}
      />
        {
          errors.nameCompany?.message ? selectMessage(errors.nameCompany?.message):null
        }
    </View>

    <View style={{alignItems: 'center', padding:10}}>
      <Text style={styles.Text}>CPF:</Text>
      <Controller 
        control={control}
        name="cpfCompany"
        render={({field: {onChange, value}})=>(
          <TextInputMask 
          type={'cpf'} 
          placeholder="Digite o CPF do seu acompanhante"
          style={styles.input} 
          value={value} 
          onChangeText={value => onChange(value)}
          />
        )} 
        rules={{
          required:{
            value:true,
            message:'Digite o cpf do seu acompanhante'
          },
          pattern:{
            value:/^([0-9]{3}\.?[0-9]{3}\.?[0-9]{3}\-?[0-9]{2}|[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2})$/,
            message:'Digite um cpf valido'
          }
        }}
      />
      {errors.cpfCompany?.message ? selectMessage(errors.cpfCompany?.message):null}
    </View>
      <Button title="Assinatura" onPress={handleSubmit(onSubmit)} />
    </ScrollView>
    </KeyboardAvoidingView>
  )
}
const styles = StyleSheet.create({
  Title: {
    fontSize:20,
    padding:10,
    textAlign: 'center',
    fontWeight: 'bold', 
  },
  Text:{
    fontSize:20,
  },
  input: {
    fontSize:20,
    width: '100%',
    padding: 10,
    backgroundColor:"#00000021"
  },
});

export default SignatureStatement