import { Controller, useForm } from "react-hook-form";
import { Button, KeyboardAvoidingView, Platform, ScrollView, StyleSheet, Text, TextInput, View } from "react-native";
import { TextInputMask } from "react-native-masked-text";
import { RadioButton } from 'react-native-paper'
import { useDispatch } from "react-redux";
import { useNavigation } from "@react-navigation/native"
import { NativeStackNavigationProp } from "@react-navigation/native-stack"
import { useState } from "react";

type RootStackParams = {
  'Termo de Consentimento':any,
}

const Message = (message:String) => {
  return (
    <View>
      <Text style={{color:'red'}}>{message}</Text>
    </View>
  )
}

export default function Quiz () {
  const dispatch = useDispatch()
  const [checkExam, setCheckExam] = useState(false)
  const navigation = useNavigation<NativeStackNavigationProp<RootStackParams>>()

  const onSubmit = (data:any) => {
    dispatch({
      type:'SET_PREPROCEDURE',
      payload:{
        data
      }
    })
    navigation.navigate('Termo de Consentimento')
  }
  const {handleSubmit, control, formState:{errors}} = useForm()

  const selectMessage = (data:string) =>{
    return Message(data)
  }

  return (

    <KeyboardAvoidingView 
    behavior={Platform.select({ios:'padding'})} 
    keyboardVerticalOffset={Platform.select({ios:110})}
    >
      <ScrollView style={{backgroundColor:"white", height:'100%'}}>
          
        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Nome Completo</Text>
          <Controller 
            control={control}
            name="name"
            render={({field: {onChange, value}})=>(
              <TextInput 
                placeholder="Digite aqui seu Nome" 
                style={styles.input} 
                value={value} 
                onChangeText={value => onChange(value)}
              />
            )}
            rules={{
              required:{
                value: true,
                message: 'Digite seu nome'
              }
            }}
          />
          {
            errors.name?.message ? selectMessage(errors.name?.message):null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>CPF</Text>
          <Controller 
            control={control}
            name='cpf'
            render={({field: {onChange, value}})=>(
              <TextInputMask
                type="cpf"
                placeholder="Digite aqui seu CPF"
                style={styles.input}
                value={value}
                onChangeText={value => onChange(value)}
              />
            )}
            rules={{
              required:{
                value:true,
                message:'Digite seu cpf'
              },
              pattern:{
                value:/^([0-9]{3}\.?[0-9]{3}\.?[0-9]{3}\-?[0-9]{2}|[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2})$/,
                message:'Digite um cpf valido'
              }
            }}
          />
          {errors.cpf?.message ? selectMessage(errors.cpf?.message):null}
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Email</Text>
          <Controller 
            control={control}
            name="email"
            render={({field: {onChange, value}})=>(
              <TextInput
              placeholder="Digite aqui seu Email" 
              style={styles.input} 
              value={value} 
              onChangeText={value => onChange(value.trim())}
              />
            )}
            rules={{
              required:{
                value: true,
                message: 'digite seu email'
              },
              pattern:{
                value: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/,
                message: 'Digite um email valido'
              }
            }}
          />
          {
            errors.email?.message? selectMessage(errors.email?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Data de Nascimento</Text>
          <Controller
            control={control}
            name="birthDate"
            render={({field: {onChange, value}})=>(
              <TextInputMask
                placeholder="Ex: 25/04/1995" 
                type={'datetime'} 
                options={{
                    format:'DD/MM/YYYY'
                  }}
                style={styles.input} 
                value={value}  
                onChangeText={value => onChange(value)} 
              />
            )}
            rules={{
              required:{
                value: true,
                message: 'Digite sua Data de Nascimento'
              },
              pattern:{
                value:/^(0?[1-9]|[12][0-9]|3[01])[\/\-](0?[1-9]|1[012])[\/\-]\d{4}$/,
                message: 'Digite uma data valida'
              }
            }}
          />
          {
            errors.birthDate?.message? selectMessage(errors.birthDate?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Peso</Text>
          <Controller 
            control={control}
            name='weight'
            render={({field: {onChange, value}})=> (
              <TextInputMask
                type='only-numbers' 
                placeholder="Ex: 80"
                style={styles.input} 
                value={value} 
                onChangeText={value=> onChange(value)}
              />
            )}
            rules={{
              required:{
                value:true,
                message: 'Digite seu Peso'
              }
            }}
          />
          {
            errors.weight?.message? selectMessage(errors.weight?.message): null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Altura</Text>
          <Controller 
            control={control}
            name='height'
            render={({field: {onChange, value}})=>(
              <TextInputMask 
                type='only-numbers' 
                placeholder="Ex: 179"
                style={styles.input}
                value={value} 
                onChangeText={value=> onChange(value)}
              />
            )}
            rules={{
              required:{
                value:true,
                message:'Digite sua Altura'
              }}
            }
          />
          {
            errors.height?.message? selectMessage(errors.height?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>De que forma vai para casa após o exame?</Text>
          <Controller 
            control={control}
            name="drivingHome"
            render={({field: {onChange, value}})=>(
              <TextInput
                placeholder="Ex: Dirigindo, Carona, Uber, etc..."
                style={styles.input} 
                value={value} 
                onChangeText={value => onChange(value)}
              />
            )}
            rules={{
              required:{
                value:true,
                message:'Digite a forma que você vai para casa'
              }
            }}
          />
          {
          errors.drivingHome?.message? selectMessage(errors.drivingHome?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Motivo do exame</Text>
          <Controller 
            control={control}
            name='reasonForExam'
            render={({field: {onChange, value}})=>(
              <TextInput 
              placeholder="Check-up Anual"
              style={styles.input} 
              value={value} 
              onChangeText={value => onChange(value)}
              />
            )}
            rules={{
              required:{
                value: true,
                message:'Digite o motivo do exame'
              }
            }}
          />
          {
            errors.reasonForExam?.message? selectMessage(errors.reasonForExam?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Já fez este exame antes?</Text>
              <Controller 
                control={control}
                name='checkIsExam'
                render={({field: {onChange, value}})=>(
                <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                  <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                    <View style={{flexDirection:'row', alignItems: 'center'}}>
                      <RadioButton.Android
                        value='Yes'
                        color="green"
                        status={value === 'Yes' ? 'checked' : 'unchecked'}
                        onPress={()=> {
                          onChange('Yes')
                          setCheckExam(true)
                        }}
                      />
                      <Text style={styles.Text}>Sim</Text>
                    </View>
                    <View style={{flexDirection:'row', alignItems: 'center'}}>
                      <RadioButton.Android
                        value="No"
                        color='red'
                        status={value === 'No' ? 'checked' : 'unchecked'}
                        onPress={() => {
                          onChange('No')
                          setCheckExam(false)
                        }}
                      />
                      <Text style={styles.Text}>Não</Text>
                    </View>
                    </View>
                    {                      
                      value === 'Yes'
                      ?
                       <View style={{alignItems: 'center',width:'100%'}}>
                          <Text style={styles.Text}>Qual Motivo?</Text>
                          <Controller
                            control={control}
                            name="reasonIsExam"
                            render={({field: {onChange, value}})=>(
                              <TextInput 
                                placeholder="Ex: Dor na barriga"
                                style={styles.input} 
                                value={value} 
                                onChangeText={value=> onChange(value)}
                                />
                            )}
                            rules={{
                              required:{
                                value:true,
                                message:'Digite o motivo'
                              }
                            }}
                          />
                          {
                            errors.reasonIsExam?.message? selectMessage(errors.reasonIsExam?.message) :null
                          }
                        </View> 
                        :null
                        
                    } 
                  </View> 
                )}
                rules={{
                  required: {
                    value: true,
                    message: 'Selecione o campo'
                  }
                }}
              />
              {
                errors.checkIsExam?.message? selectMessage(errors.checkIsExam?.message) :null
              }
        </View>
        {
          checkExam
          ?
          <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Teve algum problema durante o exame?</Text>
          <Controller 
            control={control}
            name='checkProblemExam'
            render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
              <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
                </View>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value="No"
                    color='red'
                    status={value === 'No' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('No')}}
                  />
                  <Text style={styles.Text}>Não</Text>
                </View>
            </View>
            {
              value === 'Yes'
              ?
              <View style={{alignItems: 'center',width:'100%'}}>
                <Text style={styles.Text}>Qual Problema?</Text>
                <Controller 
                  control={control}
                  name="whatsProblem"
                  render={({field: {onChange, value}})=>(
                    <TextInput 
                      placeholder="Ex: Sangramento"
                      style={styles.input} 
                      value={value} 
                      onChangeText={value => onChange(value)}
                    />
                  )}
                  rules={{
                    required:{
                      value:true,
                      message:'Digite a complicação durante o exame'
                    }
                  }}
                />
                {
                  errors.whatsProblem?.message? selectMessage(errors.whatsProblem?.message) :null
                }
              </View>
            :null
            }
            </View>
            )}
            rules={{
              required: {
                value:true,
                message:'Selecione o campo'
              }
            }}
          />
          {
            errors.checkProblemExam?.message? selectMessage(errors.checkProblemExam?.message) :null
          }
        </View>
        :null
        }

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem alergia a algum medicamento?</Text>
            <Controller 
              control={control}
              name="checkDrugAllergy"
              render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
                </View>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value="No"
                    color='red'
                    status={value === 'No' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('No')}}
                  />
                  <Text style={styles.Text}>Não</Text>
                </View>
              </View>
              {
                value === 'Yes'
                ?
                <View style={{alignItems: 'center',width:'100%'}}>
                  <Text style={styles.Text}>Qual medicamento?</Text>
                  <Controller 
                    control={control}
                    name='medicationsCausedAllergies'
                    render={({field: {onChange, value}})=>(
                      <TextInput 
                        placeholder="Ex: Dipirona"
                        style={styles.input} 
                        value={value} 
                        onChangeText={value => onChange(value)}
                      />
                    )}
                    rules={{
                      required: {
                        value:true,
                        message:'Digite qual medicamento causa alergia'
                      }
                    }}
                  />
                  {
                    errors.medicationsCausedAllergies?.message? selectMessage(errors.medicationsCausedAllergies?.message) :null
                  }
                </View>
                :null
              }
              </View>
              )}
              rules={{
                required: {
                  value:true,
                  message:'Selecione o campo'
                }
              }}
            />
            {
              errors.checkDrugAllergy?.message? selectMessage(errors.checkDrugAllergy?.message) :null
            }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem algum problema de saúde?</Text>
            <Controller 
              control={control}
              name='checkProblemHealth'
              render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                  <View style={{flexDirection:'row', alignItems: 'center'}}>
                    <RadioButton.Android
                      value='Yes'
                      color="green"
                      status={value === 'Yes' ? 'checked' : 'unchecked'}
                      onPress={() => {onChange('Yes')}}
                    />
                    <Text style={styles.Text}>Sim</Text>
                  </View>
                  <View style={{flexDirection:'row', alignItems: 'center'}}>
                    <RadioButton.Android
                      value="No"
                      color='red'
                      status={value === 'No' ? 'checked' : 'unchecked'}
                      onPress={() => {onChange('No')}}
                    />
                    <Text style={styles.Text}>Não</Text>
                  </View>
                </View>
                {
                  value === 'Yes'
                  ?
                  <View style={{alignItems: 'center',width:'100%'}}>
                    <Text style={styles.Text}>Qual problema de Saúde?</Text>
                    <Controller 
                      control={control}
                      name='whatsProblemHealth'
                      render={({field: {onChange, value}})=>(
                        <TextInput 
                          placeholder="Ex: Rinite"
                          style={styles.input} 
                          value={value} 
                          onChangeText={value => onChange(value)}
                        />
                      )}
                      rules={{
                        required: {
                          value:true,
                          message:'Digite o problema de Saúde'
                        }
                      }}
                    />
                    {
                      errors.whatsProblemHealth?.message? selectMessage(errors.whatsProblemHealth?.message) :null
                    }
                  </View>
                  :null
                }
              </View>
              )}
              rules={{
                required: {
                  value:true,
                  message:'Selecione o campo'
                }
              }}
            />
            {
              errors.checkProblemHealth?.message? selectMessage(errors.checkProblemHealth?.message) :null
            }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Já fez alguma cirurgia?</Text>
          <Controller 
            control={control}
            name='checkHadSurgery'
            render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
                </View>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value="No"
                    color='red'
                    status={value === 'No' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('No')}}
                  />
                  <Text style={styles.Text}>Não</Text>
                </View>
              </View>
              {
                value === 'Yes'
                ?
                <View style={{alignItems: 'center',width:'100%'}}>
                  <Text style={styles.Text}>Qual?</Text>
                  <Controller 
                    control={control}
                    name='whatsSergery'
                    render={({field: {onChange, value}})=>(
                      <TextInput 
                        placeholder="Ex: Bariátrica"
                        style={styles.input} 
                        value={value} 
                        onChangeText={value => onChange(value)}
                      />
                    )}
                    rules={{
                      required: {
                        value: true,
                        message:'Digite qual cirgurgia'
                      }
                    }}
                  />
                  {
                    errors.whatsSergery?.message? selectMessage(errors.whatsSergery?.message) :null
                  }
                </View>
                :null
              }
            </View>
            )}
            rules={{
              required:{
                value: true,
                message:'Seleciona o campo'
              }
            }}
          />
          {
            errors.checkHadSurgery?.message? selectMessage(errors.checkHadSurgery?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Você ou alguém da sua familia já teve cancer?</Text>
          <Controller 
            control={control}
            name='checkFamilialCancer'
            render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
                </View>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value="No"
                    color='red'
                    status={value === 'No' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('No')}}
                  />
                  <Text style={styles.Text}>Não</Text>
                </View>
              </View>
              {
                value === 'Yes'
                ?
                <View style={{alignItems: 'center',width:'100%'}}>
                  <Text style={styles.Text}>Qual câncer?</Text>
                  <Controller
                    control={control}
                    name='whatsCancer'
                    render={({field: {onChange, value}})=>(
                      <TextInput 
                        placeholder="Ex: Câncer de Mama"
                        style={styles.input} 
                        value={value} 
                        onChangeText={value=>onChange(value)}
                      />
                    )}
                    rules={{
                      required:{
                        value:true,
                        message:'digite o tipo de cancer'
                      }
                    }}
                  />
                  {
                    errors.whatsCancer?.message? selectMessage(errors.whatsCancer?.message) :null
                  }
                  <Text style={styles.Text}>Quando?</Text>
                    <Controller 
                      control={control}
                      name='whenCancer'
                      render={({field: {onChange, value}})=>(
                        <TextInputMask
                        placeholder="Ex: 21/08/2013" 
                        options={{
                          format:'DD/MM/YYYY'
                        }}
                        type={'datetime'} 
                        style={styles.input} 
                        value={value} 
                        onChangeText={value=>onChange(value)} 
                      />
                      )}
                      rules={{
                        required:{
                          value:true,
                          message: 'digite quando teve cancer'
                        },
                        pattern:{
                          value:/^(0?[1-9]|[12][0-9]|3[01])[\/\-](0?[1-9]|1[012])[\/\-]\d{4}$/,
                          message: 'Digite uma data valida'
                        }
                      }}
                    />
                    {
                      errors.whenCancer?.message? selectMessage(errors.whenCancer?.message) :null
                    }
                </View>
                :null
              }
            </View>
            )}
            rules={{
              required:{
                value: true,
                message:'Seleciona o campo'
              }
            }}
          />
          {
            errors.checkFamilialCancer?.message? selectMessage(errors.checkFamilialCancer?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Toma medicamentos todos os dias?</Text>
          <Controller 
            control={control}
            name='checkMedicationEveryDay'
            render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
                </View>
                <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value="No"
                    color='red'
                    status={value === 'No' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('No')}}
                  />
                  <Text style={styles.Text}>Não</Text>
                </View>
              </View>
              {
                value === 'Yes'
                ?
                <View style={{alignItems: 'center',width:'100%'}}>
                  <Text style={styles.Text}>Qual Remédio?</Text>
                  <Controller 
                    control={control}
                    name='whatsRemedyEveryDay'
                    render={({field: {onChange, value}})=>(
                      <TextInput 
                        placeholder="Ex: Dipirona"
                        style={styles.input} 
                        value={value} 
                        onChangeText={value => onChange(value)}
                      />
                    )}
                    rules={{
                      required:{
                        value:true,
                        message:'Digite qual remédio'
                      }
                    }}
                  />
                  {
                    errors.whatsRemedyEveryDay?.message? selectMessage(errors.whatsRemedyEveryDay?.message) :null
                  }
                </View>
                :null
              }
            </View>
            )}
            rules={{
              required:{
                value:true,
                message:'Selecion o campo'
              }
            }}
          />
          {
            errors.checkMedicationEveryDay?.message? selectMessage(errors.checkMedicationEveryDay?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem doença cardíaca?</Text>
          <Controller 
            control={control}
            name='checkHeartDisease'
            render={({field: {onChange, value}})=>(
              <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value='Yes'
                  color="green"
                  status={value === 'Yes' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('Yes')}}
                />
                <Text style={styles.Text}>Sim</Text>
              </View>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value="No"
                  color='red'
                  status={value === 'No' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('No')}}
                />
                <Text style={styles.Text}>Não</Text>
              </View>
            </View>
            )}
            rules={{
              required:{
                value:true,
                message:'Selecione o campo'
              }
            }}
          />
          {
            errors.checkHeartDisease?.message? selectMessage(errors.checkHeartDisease?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem Diabetes?</Text>
          <Controller 
            control={control}
            name='checkDiabetes'
            render={({field: {onChange, value}})=>(
            <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                  <RadioButton.Android
                    value='Yes'
                    color="green"
                    status={value === 'Yes' ? 'checked' : 'unchecked'}
                    onPress={() => {onChange('Yes')}}
                  />
                  <Text style={styles.Text}>Sim</Text>
              </View>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value="No"
                  color='red'
                  status={value === 'No' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('No')}}
                />
                <Text style={styles.Text}>Não</Text>
              </View>
            </View>
            )}
            rules={{
              required:{
                value:true, 
                message: 'Seleciona o campo'
              }
            }}
          />
          {
            errors.checkDiabetes?.message? selectMessage(errors.checkDiabetes?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem Marcapasso cardíaco?</Text>
          <Controller 
            control={control}
            name='checkPacemaker'
            render={({field: {onChange, value}})=>(
              <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value='Yes'
                  color="green"
                  status={value === 'Yes' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('Yes')}}
                />
                <Text style={styles.Text}>Sim</Text>
              </View>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value="No"
                  color='red'
                  status={value === 'No' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('No')}}
                />
                <Text style={styles.Text}>Não</Text>
              </View>
            </View>
            )}
            rules={{
              required:{
                value:true,
                message:'Selecione o campo'
              }
            }}
          />
          {
            errors.checkPacemaker?.message? selectMessage(errors.checkPacemaker?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Tem alguma outra doença crônica?</Text>
          <Controller 
            control={control}
            name='checkChronicDisease'
            render={({field: {onChange, value}})=>(
              <View style={{alignItems: 'center', padding:10, width:'100%'}}>  
                <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
                  <View style={{flexDirection:'row', alignItems: 'center'}}>
                    <RadioButton.Android
                      value='Yes'
                      color="green"
                      status={value === 'Yes' ? 'checked' : 'unchecked'}
                      onPress={() => {onChange('Yes')}}
                    />
                    <Text style={styles.Text}>Sim</Text>
                  </View>
                  <View style={{flexDirection:'row', alignItems: 'center'}}>
                    <RadioButton.Android
                      value="No"
                      color='red'
                      status={value === 'No' ? 'checked' : 'unchecked'}
                      onPress={() => {onChange('No')}}
                    />
                    <Text style={styles.Text}>Não</Text>
                </View>
              </View>
              {value === 'Yes'
              ?
              <View style={{alignItems: 'center',width:'100%'}}>
                <Text style={styles.Text}>Qual doença crônica?</Text>
                <Controller 
                  control={control}
                  name='whatsChronicDisease'
                  render={({field: {onChange, value}})=>(
                    <TextInput
                    placeholder="Ex: Asma"
                    style={styles.input}
                    value={value}
                    onChangeText={value=>onChange(value)}
                    />
                  )}
                  rules={{
                    required:{
                      value:true,
                      message:'Digita a doença'
                    }
                  }}
                />
                {
                  errors.whatsChronicDisease?.message? selectMessage(errors.whatsChronicDisease?.message) :null
                }
              </View>
              :null
            }
            </View>
            )}
            rules={{
              required:{
                value:true,
                message:'Selecione o campo'
              }
            }}
          />
          {
            errors.checkChronicDisease?.message? selectMessage(errors.checkChronicDisease?.message) :null
          }
        </View>

        <View style={{alignItems: 'center', padding:10}}>
          <Text style={styles.Text}>Apresentou algum sangramento nos últimos 12 meses?</Text>
          <Controller 
            control={control}
            name='checkBleeding'
            render={({field: {onChange, value}})=>(
              <View style={{flexDirection:'row', width:'100%', justifyContent: 'space-evenly'}}>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value='Yes'
                  color="green"
                  status={value === 'Yes' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('Yes')}}
                />
                <Text style={styles.Text}>Sim</Text>
              </View>
              <View style={{flexDirection:'row', alignItems: 'center'}}>
                <RadioButton.Android
                  value="No"
                  color='red'
                  status={value === 'No' ? 'checked' : 'unchecked'}
                  onPress={() => {onChange('No')}}
                />
                <Text style={styles.Text}>Não</Text>
              </View>
            </View>
            )}
            rules={{
              required: {
                value:true,
                message:'Selecione o campo'
              }
            }}
          />
          {
            errors.checkBleeding?.message? selectMessage(errors.checkBleeding?.message) :null
          }
        </View>

        <Button title="Finalizar" onPress={handleSubmit(onSubmit)} />
      </ScrollView>
    </KeyboardAvoidingView>
  )
}
const styles = StyleSheet.create({
  Title: {
    fontSize:20,
    padding:1,
    textAlign: 'center',
    fontWeight: 'bold', 
  },
  SubTitle: {
    fontSize:20,
    textAlign: 'center',
    fontWeight: 'bold', 
  },
  Text:{
    fontSize:20,
  },
  input: {
    fontSize:20,
    width: '100%',
    padding: 10,
    backgroundColor:"#00000021"
  },
});