import { useState } from "react"
import Checkbox from "expo-checkbox"
import {StyleSheet,ScrollView,SafeAreaView, Image, Text, View, Button } from "react-native"
import { useDispatch } from "react-redux"
import { useNavigation } from "@react-navigation/native"
import { NativeStackNavigationProp } from "@react-navigation/native-stack"

type RootStackParams = {
  SignatureStatement:any,
}

export default function HomeScreen () {
  const [isSelectPolips, setIsSelectPolips] = useState(false)
  const [isSelectStatement, setisSelectStatement] = useState(false)
  const dispatch = useDispatch()
  const navigation = useNavigation<NativeStackNavigationProp<RootStackParams>>()

  const requirementsForNextPage = () => {
    dispatch({ 
      type:'SET_ACCEPT_POLIPS',
      payload:{
        isSelectStatement: isSelectStatement,
        isSelectPolips: isSelectPolips
      }
    })
    navigation.navigate('SignatureStatement')
  }

  return(
   <View style={{backgroundColor:'white'}}>

    <SafeAreaView style={styles.container}>
    <ScrollView style={styles.scrollView}>

     <Text style={styles.Texts}>
      O termo de consentimento é um documento de informação e aceite dos riscos e
      benefícios dos procedimentos propostos pela equipe médica. É o processo pelo
      qual o paciente e/ou seu representante legal dá permissão ou recusa submeter-se
      a um procedimento médico. É obrigatório por lei e, na eventualidade da negativa de
      uma das partes em assiná-lo, o procedimento em questão não poderá ser realizado.
      </Text>
     <Text style={styles.Texts}>
      Os exames endoscópicos Endoscopia Digestiva Alta (EDA) e Colonoscopia
      caracterizam-se pela aquisição de imagens em tempo real da superfície interna do
      Esôfago, Estômago e Duodeno, no caso da EDA e do Intestino Grosso e a parte
      final do Intestino Delgado, na Colonoscopia. Para tal análise, é importante que
      estes órgãos se encontrem vazios e, portanto, necessitam de preparo com jejum
      para a EDA e jejum mais preparo colônico para a Colonoscopia. É obrigatório o
      preenchimento do questionário pré-procedimento e o jejum de 08(oito) horas antes
      do exame. Caso esses termos não sejam realizados, seu exame será imediatamente cancelado.
     </Text>
     <Text style={styles.Texts}>
      Tanto a EDA como a Colonoscopia são realizados sob sedação conforto do paciente.
     </Text>
     <Text style={styles.Texts}>
      Como em qualquer procedimento, os exames endoscópicos podem gerar efeitos
      adversos inerentes dos próprios procedimentos, de seu preparo e medicações
      independentes da perícia e expertise da equipe. Dentre estes, os mais
      comuns:Flebite (inflamação no local da aplicação dos medicamentos e soro);
      Hematomas no local da infusão dos medicamentos;Reações alérgicas antes
      desconhecidas;Embolias (formação e migração de pequenos coágulos);Depressão
      respiratória (diminuição da oxigenação sanguínea);Hipotensão (queda de
      pressão);Hipoglicemia (queda das taxas de açúcar);Desidratação;Náuseas e
      vômitos;Descompensação de doenças já existentes;Sensibilidade ou discreto
      dolorimento na garganta;Desconfortoe dor abdominal após o exame;Bacteremia
      (tremores, sensação de frio intenso);Febre e Infecções, entre outros.
     </Text>
     <Text style={styles.Texts}>
      <Text style={{fontWeight:'bold'}}>Complicações, apesar de raras também podem ocorrer:</Text> até 0,1% dos casos
      poderão ocorrer perfurações, requerendo internação e eventual tratamento
      cirúrgico; em até 0,6% dos casos poderão ocorrer hemorragias requerendo
      internação com eventual tratamento cirúrgico, se necessário, e transfusão
      sanguínea; em até 8% dos casos pode ocorrer bacteremia transitória e, raramente,
      infecção generalizada com risco de tratamento prolongado. Infecções prolongadas
      são extremamente raras.
     </Text>
     <Text style={styles.Texts}>
       Esclarecemos que durante os exames endoscópicos, tanto na EDA como na
      Colonoscopia podem ser identificadas lesões como pólipos, entre outras lesões,
      que necessitem ser retiradas por diferentes técnicas na dependência de suas
      características, tamanho e localização, entre outros fatores. A ressecção de lesões
      e/ou a realização de outros procedimentos tem maiores riscos de complicações e destas,
      as mais frequentes são sangramentos e perfurações.
     </Text>
     <Text style={styles.Texts}>
      Esclarecemos que trabalhamos com os mais rígidos critérios de segurança e
      estamos preparados para atender a todas possíveis complicações.
     </Text>
     <Text style={styles.Texts}>
        Diante do acima exposto, declaro que:
     </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>1.</Text>Eu recebi,li e autorizo a equipe de Endoscopia Digestiva o Informativo sobre o
      procedimento denominado ENDOSCOPIA DIGESTIVA ALTA e/ou COLONOSCOPIA.
      Eu compreendi que serei submetido a um procedimento, de acordo com a
      solicitação do meu médico assistente, que envolve a passagem de um aparelho
      (endoscópio e/ou colonoscópio) para o médico examinar o interior do Esôfago,
      Estomago e Duodeno e/ou Intestino grosso e parte do intestino delgado e, se
      necessário, autorizo a realizar biópsias (retirada de fragmentos da mucosa para
      exame) ou outros procedimentos.
    </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>2.</Text>Eu compreendi que o exame poderá ser incompleto ou lesões poderão passar
      despercebidas, em caso de preparo insatisfatório, ou seja, haja restos alimentares
      ou fezes no interior dos órgãos a serem examinados. O exame poderá ser
      suspenso e ser necessária uma nova marcação e novo preparo intestinal.
    </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>3.</Text>O exame ainda poderá ser incompleto quando houver alguma obstrução ou
      dificuldade técnica para a passagem do aparelho.
    </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>4.</Text>Sei que serei submetido à sedação através da administração de medicação
      venosa ou à anestesia geral venosa, conforme o meu estado clínico e julgamento
      do médico da equipe.
     </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>5.</Text>Compreendi que, embora em menos de 1% dos casos, podem ocorrer
      complicações decorrentes da aplicação da sedação (dor ou inflamação no local da
      injeção). Ainda mais raramente, podem ocorrer reações à medicação e problemas
      cardiorrespiratórios durante o procedimento.
     </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>6.</Text>Compreendi que durante o exame diagnóstico pode ser necessário realizar
      algum procedimento terapêutico como: injeção de substâncias para deter
      sangramento que esteja em curso, remoção de pólipos com uso de bisturi elétrico
      ou dilatações. Quando removidos e recuperados, os pólipos e outras lesões serão
      enviados para análise histopatológica. Autorizo a equipe médica a realizar esses
      procedimentos.
     </Text>
     <View style={{flexDirection: 'row', padding:30, alignItems: 'center'}}>
     <Checkbox
        style={{margin:10}}
        value={isSelectPolips}
        onValueChange={()=>{setIsSelectPolips(true)}}
        />
        <Text style={{fontWeight:'bold', fontStyle:'italic'}}>Autorizo a retirada de pólipos?(Até Três Pólipos por seguimento, acréscimo R$200,00)</Text>
    </View>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>7.</Text>Sei que esses procedimentos terapêuticos, são importantes no meu tratamento,
      mas aumentam o risco de complicações como sangramentos e perfurações. A
      equipe médica me explicou que estas complicações, embora incomuns, podem ser
      sérias, podendo resultar na necessidade de hospitalização, até mesmo em UTI, por
      tempo prolongado, transfusão sanguínea, procedimento endoscópico adicional,
      cirurgia de urgência e, apesar de raríssimo, risco de morte. Entendo e concordo
      com os riscos que serei submetido no exame.
    </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>8.</Text>Sei que apesar de tais riscos, estes procedimentos representam a melhor opção
      para continuidade do meu diagnóstico e/ou tratamento. Sei que posso revogar
      esse termo a qualquer momento, de acordo com a legislação vigente ou recusar-
      me a ser submetido a este procedimento.
    </Text>
    <Text style={styles.Texts}>
    <Text style={{fontWeight:'bold'}}>9.</Text>Sei que em caso de dúvidas, sinais ou sintomas que julgar estranhos após o
      procedimento poderei entrar em contato com o Serviço de Endoscopia pelo
      telefone (21) 99938-8200. Estou ciente que a equipe médica não atende urgências
      e, caso isso ocorra, devo procurar o Pronto-socorro mais próximo, sob minha
      responsabilidade da escolha e dos custos ora cobrados.
    </Text>
     <Text style={styles.Texts}>
     <Text style={{fontWeight:'bold'}}>10.</Text>Sei que mantenho o direito de revogar a qualquer momento este Termo de
      Consentimento antes que o procedimento objeto deste documento tenha se
      realizado.
     </Text>
     <Text style={styles.Texts}>
     <Text style={{fontWeight:'bold'}}>11.</Text>Declaro que me foram fornecidas todas estas informações, verbalmente e por
      escrito, em linguagem dentro dos limites de minha compreensão, e que todas as
      dúvidas em relação ao procedimento foram esclarecidas. E que autorizo a
      divulgação das imagens colhidas e dos meus dados pessoais.
      12.Declaro estar ciente que não poderei dirigir ou realizar atividades que
      necessitem de atenção, não devendo ficar sozinho por um período de 12 a 24
      horas após o exame.
     </Text>
     <Text style={styles.Texts}>
     <Text style={{fontWeight:'bold'}}>
        OBS: Para esse exame, é obrigatória a presença de um acompanhante maior de
        18 anos para a volta para casa. De acordo com a Resolução do Conselho Federal
        de Medicina 1886/2008, sem a presença de um acompanhante, o procedimento
        não poderá ser realizado.
     </Text>

     </Text>
     <View style={{flexDirection: 'row', padding:20, alignItems: 'center'}}>
     <Checkbox
     style={{height:30, width:30}}
        value={isSelectStatement}
        onValueChange={setisSelectStatement}
        />
      <Text style={{fontStyle:'italic',textAlign:'center',width:'90%'}}> 
        Estamos ciente do procedimento a que serei submetido e confirmo a leitura deste
        termo de consentimento livre e esclarecido (TCLE).
      </Text>

    </View>
    <Button disabled={!isSelectStatement} title="Proxima pagina" onPress={()=>{requirementsForNextPage()}} />
    </ScrollView>
    </SafeAreaView>
     </View>
  )
}

const styles = StyleSheet.create({
  container: {
  },
  scrollView: {
    marginHorizontal: 20,
    height:'100%'
  },
  Title: {
    fontSize:20,
    padding:10,
    textAlign: 'center',
    fontWeight: 'bold'
  },
  Texts:{
    padding: 10,
    alignItems:'center',
  }
});