import {View, Text, StyleSheet, Platform} from 'react-native'
import { useSelector } from 'react-redux';
import Logo from './Logo';

export default function HeaderCamera () {
  const TitleCamera = useSelector((state:any)=>state.data.titleCamera)
  var data = new Date();
  var dia = String(data.getDate()).padStart(2, '0');
  var mes = String(data.getMonth() + 1).padStart(2, '0');
  var ano = data.getFullYear();
  let dataAtual = dia + '/' + mes + '/' + ano;

  return (
    <View style={{flexDirection:'column',justifyContent: 'flex-end', alignItems: 'center', height:Platform.select({ios:150,android:200})}}>
      <Logo />
      <Text style={styles.Title}>{TitleCamera}</Text>
      <Text style={styles.SubTitle}>{`${dataAtual} - ${data.toLocaleTimeString()}`}</Text>
    </View>
  )
}

const styles = StyleSheet.create({
  Title: {
    fontSize:20,
    padding:1,
    textAlign: 'center',
    fontWeight: 'bold', 
  },
  SubTitle: {
    fontSize:14,
    textAlign: 'center',
    fontWeight: 'bold', 
  },
  Text:{
    fontSize:20,
  },
  input: {
    fontSize:20,
    width: '100%',
    padding: 10,
    backgroundColor:"#00000021"
  },
});