import React, { useEffect, useState } from "react";
import { View, Image, Button, Text } from "react-native";
import Signature from "react-native-signature-canvas";
import { useDispatch, useSelector } from "react-redux";
import { useNavigation } from "@react-navigation/native";
import { NativeStackNavigationProp } from "@react-navigation/native-stack";

type RootStackParams = {
  'Camera':any,
}

export const  SignatureScreen  =  () => {
  const [signature, setSign] = useState(null)
  const [signaturePatient, setSignPatient] = useState(null)
  const [signatureCompany, setSignCompany] = useState(null)
  const [clear, setClear] = useState(false)
  const dispatch = useDispatch()
  const navigation = useNavigation<NativeStackNavigationProp<RootStackParams>>()
  
  const shareSignature = () => {

    console.log()
    dispatch({
      type:'SET_SIGNATURE',
      payload: {
        signaturePatient:signaturePatient,
        signatureCompany:signatureCompany
      }
    })
    navigation.navigate('Camera')
  }
 

  const handleOK = (signature:any) => {
    setSign(signature);
  };

  const SignaturePatient = () => {
    setSignPatient(signature)
    setSign(null);
    setClear(true)
  }
  
  const SignatureCompany = () => {
    setSignCompany(signature)
    setClear(true)
  }

  const handleEmpty = () => {
    console.log("Empty");
  };

  const style = `.m-signature-pad--footer
      .button {
      background-color: red;
      color: #FFF;
    }`;
    
  return (
    <View style={{flex:1}}>
      <View style={{flex:6}}>
        <Text style={{textAlign: 'center', fontSize:24}}>{
        signaturePatient
        ?"Assinatura do Acompanhante"
        :"Assinatura do Paciente"
        }
        </Text>
        
        <Signature
          onOK={handleOK}
          onEmpty={handleEmpty}
          descriptionText="Sign"
          autoClear={clear}
          clearText="Limpar"
          onClear={()=>{setSign(null)}}
          confirmText="Visualizar"
          webStyle={style}
          />
      </View>
      <View style={{flex:3}}>
        {signature 
        ?  <Image
        resizeMode={"contain"}
        style={{ width: '100%', height: '100%'}}
        source={{ uri: signature }}
      />
        :null
        }
      </View>
      <View style={{flex:1}}>
        {
          signature 
          ? signaturePatient
            ? signatureCompany
              ?<Button title='Tirar foto' onPress={()=> shareSignature()} />
              :<Button title="Confirmar Assinatura Acompanhate" onPress={()=>SignatureCompany()}/>
            :<Button title="Confirmar Assinatura Paciente" onPress={()=>SignaturePatient()}/>
          :null 
        }
        
      </View>
    </View>
  );
};

export default SignatureScreen