import * as React from 'react'
import { PersistGate } from 'redux-persist/es/integration/react'
import { Provider } from 'react-redux'
import { NavigationContainer } from '@react-navigation/native'
import { createNativeStackNavigator } from '@react-navigation/native-stack'
import HomeScreen from './Statement-of-responsibily'
import SignatureScreen from './Sign'
import SignatureStatement from './SignatureStatement'
import {store, persistor} from './Store'
import Quiz from './Pre-procedureQuestionnaire'
import HeaderPreProcedure from './Headers/HeaderPre-procedure'
import HeaderStatementOfResponsibily from './Headers/HeaderStatementOfResponsibily'
import CameraPicture from './Camera'
import HeaderCamera from './Headers/HeaderCamera'

const Stack = createNativeStackNavigator()

export default function App() {
  return (
    <Provider store={store}>
      <PersistGate loading={null} persistor={persistor}>
      <NavigationContainer>
        <Stack.Navigator>
          <Stack.Screen 
          name="Pre-procedure" 
          component={Quiz} 
          options={{
            header:()=><HeaderPreProcedure />
          }}
          />
          <Stack.Screen 
            name="Termo de Consentimento" 
            component={HomeScreen} 
            options={{
              header:()=><HeaderStatementOfResponsibily />
            }}
            />
            <Stack.Screen 
            name="Camera" 
            component={CameraPicture} 
            options={{
              header:()=><HeaderCamera />
            }}
            />
            <Stack.Screen name="Assinatura" component={SignatureScreen} />
          <Stack.Screen name="SignatureStatement" component={SignatureStatement} />
        </Stack.Navigator>
      </NavigationContainer>
      </PersistGate>
    </Provider>
  )
}
