import React, { useState, useEffect, useRef } from 'react';
import { StyleSheet, Text, View, TouchableOpacity, Image, Button } from 'react-native';
import { Camera } from 'expo-camera';
import * as Print from 'expo-print'
import { useDispatch, useSelector } from 'react-redux';
import GenerateHTML from './pdf/pdfStatement';
import { shareAsync } from 'expo-sharing';
import { manipulateAsync } from 'expo-image-manipulator';

export default function CameraPicture() {
  const camRef:React.MutableRefObject<any> = useRef(null)
  const [hasPermission, setHasPermission] = useState(false);
  const [type, setType] = useState(Camera.Constants.Type.front);
  const [picture, setPicture] = useState('')
  const [pacientImage, setPacientImage] = useState('')
  const [idPacienteFront, setiDPacientFront] = useState('')
  const [disableOuther,setDisableOuther] = useState(false)

  const name = useSelector((state:any) =>state.data.name)
  const cpf = useSelector((state:any) =>state.data.cpf)
  const id = useSelector((state:any) =>state.data.id)
  const exam = useSelector((state:any) =>state.data.exam)
  const company = useSelector((state:any) =>state.data.company)
  const companyCpf = useSelector((state:any) =>state.data.companyCpf)
  const data = useSelector((state:any) =>state.data.data)
  const signaturePatient = useSelector((state:any) =>state.data.signaturePatient)
  const signatureCompany = useSelector((state:any) =>state.data.signatureCompany)
  const imagePatient = useSelector((state:any) =>state.data.imagePatient)
  const imageIdPatient = useSelector((state:any) =>state.data.imageIdPatient)
  const isSelectPolips = useSelector((state:any) =>state.data.isSelectPolips)

  const dispatch = useDispatch()

  const printToFile = async () => {
    
    const imagep = await manipulateAsync(
      imagePatient,
      [  {
        resize: {
          width: 300,
          height: 440
        },
      },],
      { base64: true }
      )

    const imagepid = await manipulateAsync(
      imageIdPatient,
      [  {
        resize: {
          width: 300,
          height: 440
        },
      },],
      { base64: true }
      )
      const imageP = ('data:image/png;base64,'+imagep.base64)
      const imagePID = ('data:image/png;base64,'+imagepid.base64)
      
    console.log(isSelectPolips)
    const html = await GenerateHTML(name, cpf, id, exam, signaturePatient, company, companyCpf, signatureCompany, data, imageP, imagePID, isSelectPolips)

    const { uri } = await Print.printToFileAsync({
      html
    });
    await shareAsync(uri, { UTI: '.pdf', mimeType: 'application/pdf' });
  }

  useEffect(() => {
    ( async () => {
      const { status } = await Camera.requestCameraPermissionsAsync();
      setHasPermission(status === 'granted');
    })();

  }, []);

  if (hasPermission === null) {
    return <View />;
  }
  if (hasPermission === false) {
    return <Text>No access to camera</Text>;
  }
  const __takePicture = async () => {
    if (camRef){ 
    const data = await camRef.current.takePictureAsync(); ;
    setPicture(data.uri);
   }
  }


  return (
    <View style={styles.container}>
      {
        picture === "" 
        ?
        <Camera style={styles.camera} ref={camRef} type={type}>
        <View style={styles.buttonContainer }>
          <View style={{
            alignSelf: 'flex-end',
            flex:1,
            alignItems: 'center',
          }}>
            <TouchableOpacity
              onPress={__takePicture}
              style={{
                width: 70,
                height: 70,
                bottom: 0,
                borderRadius:35,
                backgroundColor: '#fff'
              }}/>
            </View>
        </View>
      </Camera>
      :
        <View>
            <Image
            style={{ width: '100%', height: '90%'}}
            source={{ uri: picture }}
            />  
            <View style={{flexDirection:'row', justifyContent: 'space-evenly'}}>
              {
                pacientImage === ''
                ? <Button title='Salvar foto' onPress={ async () =>{
                  setPacientImage(picture)
                  setType(Camera.Constants.Type.back)
                  dispatch({
                    type:'SET_IMAGE_PATIENT',
                    payload: {
                      imagePatient: picture
                    }
                  })
                  setPicture('')
                }} />
                : idPacienteFront === ''
                  ? <Button title='Salvar foto' onPress={async () =>{
                    
                    dispatch({
                      type:'SET_IMAGE_ID_PATIENT',
                      payload: {
                        imageIdPatient: picture
                      }
                    })
                    dispatch({
                      type:'SET_TITLE_CAMERA',
                      payload: {
                        titleCamera: 'FOTO DO DOCUMENTO'
                      }
                    })
                    setiDPacientFront(picture)
                    setDisableOuther(true)
                  }} />
                  : <Button title='Enviar PDF' onPress={() =>printToFile()} />
              }
            <Button disabled={disableOuther} title="Tirar outra" onPress={() => setPicture('')} />
          </View>
        </View>
      }
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  camera: {
    flex: 1,
  },
  buttonContainer: {
    flex: 1,
    backgroundColor: 'transparent',
    justifyContent:'space-evenly',
    flexDirection: 'row',
    margin: 20,
  },
  button: {
    flex: 1,
    alignSelf: 'flex-end',
  },
  text: {
    fontSize: 18,
    color: 'white',
  },
});