import { Asset } from 'expo-asset';
import { manipulateAsync } from 'expo-image-manipulator';

type IDATA = {
  name:string,
  birthDate:any,
  weight:number,
  height:number,
  drivingHome:string,
  reasonForExam:string,
  checkIsExam:string,
  reasonIsExam:string,
  checkProblemExam:string,
  whatsProblem:string,
  checkDrugAllergy:string,
  medicationsCausedAllergies:string,
  checkProblemHealth:string,
  whatsProblemHealth:string,
  checkHadSurgery:string,
  whatsSergery:string,
  checkFamilialCancer:string,
  whenCancer:string,
  whatsCancer:string,
  checkMedicationEveryDay:string,
  whatsRemedyEveryDay:string,
  checkHeartDisease:string,
  checkDiabetes:string,
  checkPacemaker:string,
  checkChronicDisease:string,
  whatsChronicDisease:string,
  checkBleeding:string,
  cpf:string
}


export default async function GenerateHTML (
    name:string,
    cpf:string, 
    id:string, 
    exam:string, 
    signature:any, 
    company:string, 
    companyCpf:string, 
    signatureCompany:any, 
    data:IDATA, 
    imageP:any, 
    imagePID:any,
    isSelectPolips:any
    ){
  const asset = Asset.fromModule(require('../assets/support.png'));
  const image = await manipulateAsync(
    asset.localUri ?? asset.uri,
    [],
    { base64: true }
  )

  return `
  <!DOCTYPE html>
  <html lang="en">
  <head>
    <meta charset="UTF-8">
    <meta http-equiv="X-UA-Compatible" content="IE=edge">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <title>Termo de Consentimento</title>
  </head>
  <body>
  <section style="break-inside:avoid;">
  <div>
      <img style=" width:100%; background-color: white;" src="data:image/jpeg;base64,${image.base64}" alt="Carregando" />
      <h1 style= "text-align:center; font-weight:bold">Questionário Pré Procedimento</h1>
  </div>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
     Nome: ${data.name}
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    CPF: ${data.cpf}
  </p>


  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Data de Nascimento: ${data.birthDate}
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Peso: ${data.weight} Kg
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Altura: ${data.height} Cm
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    De que forma vai para casa após o exame: ${data.drivingHome}
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Motivo do exame: ${data.reasonForExam}
  </p>
  
  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Já fez este exame: ${data.checkIsExam === 'Yes' ? 'Sim':'Não'}
  </p>
  
  ${data.reasonIsExam 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual motivo: ${data.reasonIsExam}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Teve algum problema durante o exame: ${data.checkProblemExam === 'Yes' ? 'Sim':'Não'}
  </p>
 
  ${data.whatsProblem 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual motivo: ${data.whatsProblem}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem alergia a algum medicamento: ${data.checkDrugAllergy === 'Yes' ? 'Sim':'Não'}
  </p>
 
  ${data.medicationsCausedAllergies 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual medicamento: ${data.medicationsCausedAllergies}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem algum problema de saúde: ${data.checkProblemHealth === 'Yes' ? 'Sim':'Não'}
  </p>
 
  ${data.whatsProblemHealth 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual problema de Saúde: ${data.whatsProblemHealth}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Já fez alguma cirurgia: ${data.checkHadSurgery === 'Yes' ? 'Sim':'Não'}
  </p>
  
  ${data.whatsSergery 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual: ${data.whatsSergery}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Você ou alguém da sua familia já teve cancer: ${data.checkFamilialCancer === 'Yes' ? 'Sim':'Não'}
  </p>
 
  ${data.whatsCancer 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual câncer: ${data.whatsCancer}
    </p>`
    :''
  }

  ${data.whenCancer 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Quando: ${data.whenCancer}
    </p>`
    :''
  }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Toma medicamentos todos os dias: ${data.checkMedicationEveryDay === 'Yes' ? 'Sim':'Não'}
  </p>
  
  ${data.whatsRemedyEveryDay 
    ?
    `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Qual Remédio: ${data.whatsRemedyEveryDay}
    </p>`
    :''
  }
  
  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem doença cardíaca: ${data.checkHeartDisease === 'Yes' ? 'Sim':'Não'}
  </p>
  
  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem Diabetes: ${data.checkDiabetes === 'Yes' ? 'Sim':'Não'}
  </p>

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem Marcapasso cardíaco: ${data.checkPacemaker === 'Yes' ? 'Sim':'Não'}
  </p>
  
  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
  Tem alguma outra doença crônica: ${data.checkChronicDisease === 'Yes' ? 'Sim':'Não'}
  </p>
    
    ${data.whatsChronicDisease 
      ?
      `<p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
      Qual doença crônica?: ${data.whatsChronicDisease}
      </p>`
      :''
    }

  <p style="text-align:center;font-size: 18px; margin-top:10px; padding:4px 30px">
    Apresentou algum sangramento nos últimos 12 meses: ${data.checkBleeding === 'Yes' ? 'Sim':'Não'}
  </p>

  </section>
  
  <section style="break-inside:avoid;">
  <div>
    <img style=" width:100%; background-color: white;" src="data:image/jpeg;base64,${image.base64}" alt="Carregando" />
    <h1 style= "text-align:center; font-weight:bold">Termo de Consentimento informado para Exames Endoscópicos e Anestesista</h1>
  </div>


  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    O termo de consentimento é um documento de informação e aceite dos riscos e benefícios dos procedimentos propostos pela equipe médica. É o processo pelo qual o paciente e/ou seu representante legal dá permissão ou recusa submeter-se a um procedimento médico. É obrigatório por lei e, na eventualidade da negativa de uma das partes em assiná-lo, o procedimento em questão não poderá ser realizado.
  </p>

  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    Os exames endoscópicos Endoscopia Digestiva Alta (EDA) e Colonoscopia caracterizam-se pela aquisição de imagens em tempo real da superfície interna do Esôfago, Estômago e Duodeno, no caso da EDA e do Intestino Grosso e a parte final do Intestino Delgado, na Colonoscopia. Para tal análise, é importante que estes órgãos se encontrem vazios e, portanto, necessitam de preparo com jejum para a EDA e jejum mais preparo colônico para a Colonoscopia. É obrigatório o preenchimento do questionário pré-procedimento e o jejum de 08(oito) horas antes do exame. Caso esses termos não sejam realizados, seu exame será imediatamente cancelado.
  </p>

  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    Tanto a EDA como a Colonoscopia são realizados sob sedação conforto do paciente.
  </p>

  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    Como em qualquer procedimento, os exames endoscópicos podem gerar efeitos adversos inerentes dos próprios procedimentos, de seu preparo e medicações independentes da perícia e expertise da equipe. Dentre estes, os mais comuns:Flebite (inflamação no local da aplicação dos medicamentos e soro); Hematomas no local da infusão dos medicamentos;Reações alérgicas antes desconhecidas;Embolias (formação e migração de pequenos coágulos);Depressão respiratória (diminuição da oxigenação sanguínea);Hipotensão (queda de pressão);Hipoglicemia (queda das taxas de açúcar);Desidratação;Náuseas e vômitos;Descompensação de doenças já existentes;Sensibilidade ou discreto dolorimento na garganta;Desconfortoe dor abdominal após o exame;Bacteremia (tremores, sensação de frio intenso);Febre e Infecções, entre outros.
  </p>

  <p style="text-align:center;font-size: 18px; padding:53px 30px">
    <b>Complicações, apesar de raras também podem ocorrer:</b> até 0,1% dos casos poderão ocorrer perfurações, requerendo internação e eventual tratamento cirúrgico; em até 0,6% dos casos poderão ocorrer hemorragias requerendo internação com eventual tratamento cirúrgico, se necessário, e transfusão sanguínea; em até 8% dos casos pode ocorrer bacteremia transitória e, raramente, infecção generalizada com risco de tratamento prolongado. Infecções prolongadas são extremamente raras.
  </p>

  <p style="text-align:center;font-size: 18px; padding:2px 30px">
    Esclarecemos que durante os exames endoscópicos, tanto na EDA como na Colonoscopia podem ser identificadas lesões como pólipos, entre outras lesões, que necessitem ser retiradas por diferentes técnicas na dependência de suas características, tamanho e localização, entre outros fatores. A ressecção de lesões e/ou a realização de outros procedimentos tem maiores riscos de complicações e destas, as mais frequentes são sangramentos e perfurações.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    Esclarecemos que trabalhamos com os mais rígidos critérios de segurança e estamos preparados para atender a todas possíveis complicações.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    Diante do acima exposto, declaro que:
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:5px 30px">
    <b>1.</b>Eu recebi,li e autorizo a equipe de Endoscopia Digestiva o Informativo sobre o procedimento denominado ENDOSCOPIA DIGESTIVA ALTA e/ou COLONOSCOPIA. Eu compreendi que serei submetido a um procedimento, de acordo com a solicitação do meu médico assistente, que envolve a passagem de um aparelho (endoscópio e/ou colonoscópio) para o médico examinar o interior do Esôfago, Estomago e Duodeno e/ou Intestino grosso e parte do intestino delgado e, se necessário, autorizo a realizar biópsias (retirada de fragmentos da mucosa para exame) ou outros procedimentos.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>2.</b>Eu compreendi que o exame poderá ser incompleto ou lesões poderão passar despercebidas, em caso de preparo insatisfatório, ou seja, haja restos alimentares ou fezes no interior dos órgãos a serem examinados. O exame poderá ser suspenso e ser necessária uma nova marcação e novo preparo intestinal.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:3px 30px">
    <b>3.</b>O exame ainda poderá ser incompleto quando houver alguma obstrução ou dificuldade técnica para a passagem do aparelho.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:3px 30px">
    <b>4.</b>Sei que serei submetido à sedação através da administração de medicação venosa ou à anestesia geral venosa, conforme o meu estado clínico e julgamento do médico da equipe.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>5.</b>Compreendi que, embora em menos de 1% dos casos, podem ocorrer complicações decorrentes da aplicação da sedação (dor ou inflamação no local da injeção). Ainda mais raramente, podem ocorrer reações à medicação e problemas cardiorrespiratórios durante o procedimento.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  <b>6.</b>Compreendi que durante o exame diagnóstico pode ser necessário realizar algum procedimento terapêutico como: injeção de substâncias para deter sangramento que esteja em curso, remoção de pólipos com uso de bisturi elétrico ou dilatações. Quando removidos e recuperados, os pólipos e outras lesões serão enviados para análise histopatológica. Autorizo a equipe médica a realizar esses procedimentos.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  <b>
  ${
    isSelectPolips
    ?'Eu aceito a retirada de PÓLIPOS e estou ciente que existe um acréscimo de R$200,00.'
    :'Eu não aceito a retirda de PÓLIPOS.'
  }
  </b>
  </p>

  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>7.</b>Sei que esses procedimentos terapêuticos, são importantes no meu tratamento, mas aumentam o risco de complicações como sangramentos e perfurações. A equipe médica me explicou que estas complicações, embora incomuns, podem ser sérias, podendo resultar na necessidade de hospitalização, até mesmo em UTI, por tempo prolongado, transfusão sanguínea, procedimento endoscópico adicional, cirurgia de urgência e, apesar de raríssimo, risco de morte. Entendo e concordo com os riscos que serei submetido no exame.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>8.</b>Sei que apesar de tais riscos, estes procedimentos representam a melhor opção para continuidade do meu diagnóstico e/ou tratamento. Sei que posso revogar esse termo a qualquer momento, de acordo com a legislação vigente ou recusar-me a ser submetido a este procedimento.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>9.</b>Sei que em caso de dúvidas, sinais ou sintomas que julgar estranhos após o procedimento poderei entrar em contato com o Serviço de Endoscopia pelo telefone (21) 99938-8200. Estou ciente que a equipe médica não atende urgências e, caso isso ocorra, devo procurar o Pronto-socorro mais próximo, sob minha responsabilidade da escolha e dos custos ora cobrados.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>10.</b>Sei que mantenho o direito de revogar a qualquer momento este Termo de Consentimento antes que o procedimento objeto deste documento tenha se realizado.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>11.</b>Declaro que me foram fornecidas todas estas informações, verbalmente e por escrito, em linguagem dentro dos limites de minha compreensão, e que todas as dúvidas em relação ao procedimento foram esclarecidas. E que autorizo a divulgação das imagens colhidas e dos meus dados pessoais.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>12.</b>Declaro estar ciente que não poderei dirigir ou realizar atividades que necessitem de atenção, não devendo ficar sozinho por um período de 12 a 24 horas após o exame.
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
    <b>OBS: Para esse exame, é obrigatória a presença de um acompanhante maior de 18 anos para a volta para casa. De acordo com a Resolução do Conselho Federal de Medicina 1886/2008, sem a presença de um acompanhante, o procedimento não poderá ser realizado.</b>
  </p>
  </section>
  <section style="break-inside:avoid;">
  
  <p style="text-align:center;font-size: 18px;  margin-top:100px; padding:4px 30px">
  Por tanto, Eu: ${name}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  Cpf: ${cpf}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  Identidade: ${id}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  Estou ciente que serei submetido ao exame de: ${exam}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  E que meu acompanhante: ${company}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  Cpf: ${companyCpf}
  </p>
  
  <p style="text-align:center;font-size: 18px; padding:4px 30px">
  <b>estamos ciente do procedimento a que serei submetido e confirmo a leitura deste termo de consentimento livre e esclarecido (TCLE).</b>
  </p>
  
  <div style="display:flex; justify-content: center; flex-wrap: wrap;">

  <div style="display: block; margin-top:10px; padding:4px 30px; width:30%;height:20%;">
  
  <p style="text-align:center;font-size: 18px;">
    Assinatura Paciente:
  </p>
  <img
  src="${signature}"
  style="width: 100%; height:200px" />
  </div>

  <div style="display: block; margin-top:10px; padding:4px 30px;width:30%;height:20%;">
  <p style="text-align:center;font-size: 18px;">
    Foto do Paciente:
  </p>
    <img
      src="${imageP}"
      alt="Carregando"
      style="width: 100%; height:200px"
    />
  </div>
  
  <div style="display: block; margin-top:10px; padding:4px 30px;width:30%;height:20%;">
  <p style="text-align:center;font-size: 18px;">
  Documento do Paciente:
  </p>
  <img
      src="${imagePID}"
      alt="Carregando"
      style="width: 100%; height:200px" />
  </div>
  
    <div style="display: block; margin-top:10px; padding:4px 30px;width:30%;height:20%;">
      <p style="text-align:center;font-size: 18px;">
      Assinatura Acompanhante:
      </p>
      <img
      src="${signatureCompany}"
      style="width:100%; height:200px" />
    </div>
  </div>
  </section>
  </body>
  </html>
  `
}