const initialState = {
  name:'',
  cpf:'',
  id:'',
  exam:'',
  company:'',
  companyCpf:'',
  signaturePatient:'',
  signatureCompany:'',
  isSelectPolips:'',
  isSelectStatement:'',
  imagePatient:'',
  imageIdPatient:'',
  titleCamera:'Foto do Paciente',
  data:{}
}

type Action = {
  type:string,
  payload:{
    name:string,
    cpf:string,
    id:string,
    exam:string,
    imagePatient:string,
    imageIdPatient:string,
    company:string,
    titleCamera:string,
    companyCpf:string,
    signaturePatient:any,
    signatureCompany:any, 
    isSelectStatement:boolean,
    isSelectPolips:boolean,
    data:Object
  }
}

export default (state= initialState, action:Action) => {
  
  switch (action.type) {
    case 'SET_STATEMENT':
      return {...state,
        name: action.payload.name,
        cpf:action.payload.cpf,
        id: action.payload.id,
        exam: action.payload.exam,
        company: action.payload.company,
        companyCpf: action.payload.companyCpf,
      }
      break;
      case 'SET_PREPROCEDURE':
        return{...state, data: action.payload.data}
      break;
      case 'SET_ACCEPT_POLIPS':
        return{...state,
          isSelectStatement: action.payload.isSelectStatement,
          isSelectPolips: action.payload.isSelectPolips,
          }
      break;
      case 'SET_SIGNATURE':
        return{...state,
          signaturePatient: action.payload.signaturePatient,
          signatureCompany: action.payload.signatureCompany,
          }
      break;
      case 'SET_TITLE_CAMERA':
        return{...state,
          titleCamera: action.payload.titleCamera
          }
      break;
      case 'SET_IMAGE_PATIENT':
        return{...state,
          imagePatient: action.payload.imagePatient,
          }
      break;
      case 'SET_IMAGE_ID_PATIENT':
        return{...state,
          imageIdPatient: action.payload.imageIdPatient
          }
      break;
  } 
  
  return state
}